.contact {
  display: flex;
  align-items: center;
}

.contact:not(:last-child) {
  margin-bottom: 5px;
}

.link {
  font-size: 12px;
  line-height: 1.83;
  letter-spacing: 0.04em;
  color: #c2c5c9;

  transition: color 250ms var(--timingFn);
}

.link:hover,
.link:focus {
  color: var(--accentColor);
}
